@font-face {
  font-weight: 400;
  font-family: "Exo20-Thin";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Exo20-Thin.woff2") format("woff2"), url("../fonts/Exo20-Thin.woff") format("woff"); }

@font-face {
  font-weight: 400;
  font-family: "TrebuchetMS";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TrebuchetMS.woff2") format("woff2"), url("../fonts/TrebuchetMS.woff") format("woff"); }

@font-face {
  font-weight: 900;
  font-family: "TrebuchetMS";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TrebuchetMS-Bold.woff2") format("woff2"), url("../fonts/TrebuchetMS-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-size: 13px;
  line-height: 16px;
  font-weight: 400; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-main {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5) 100%), url("../img/bg-pattern.png");
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center; }

.page-main__wrap {
  width: 1280px;
  margin: 0 auto;
  padding: 37px 80px 0;
  min-height: 680px; }

.page-main__title {
  font-family: "Exo20-Thin", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
  color: #ffffff;
  margin: 0 0 22px 0; }

.page-main__list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
  min-height: 480px; }

.page-main__item {
  width: 320px;
  height: 100%;
  margin-right: 80px;
  margin-bottom: 60px;
  padding-left: 44px;
  position: relative;
  display: flex;
  flex-direction: column; }

.page-main__item:last-child {
  margin-right: 0; }

.page-main__description {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #666666;
  margin: 0;
  position: relative;
  z-index: 10; }

.page-main__item-title {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 48px;
  color: #000000;
  margin: 6px 0 4px;
  position: relative;
  z-index: 10; }

.page-main__item-type {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
  margin: 0 0 17px 0; }

.page-main__text {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #666666;
  margin: 0; }

.page-main__package {
  background: #ffffff;
  width: 100%;
  min-height: 480px;
  position: relative;
  border: 4px solid #1698d9;
  border-left: none;
  border-bottom-right-radius: 12px;
  border-top-right-radius: 12px;
  padding: 15px 20px 275px 3px;
  cursor: pointer; }
  .page-main__package::after {
    content: "";
    display: block;
    position: absolute;
    background: #ffffff;
    width: 44px;
    height: calc(100% - 37px);
    left: -43px;
    bottom: -4px;
    border-bottom: 4px solid #1698d9;
    border-left: 4px solid #1698d9;
    border-bottom-left-radius: 12px; }
  .page-main__package::before {
    content: "";
    display: block;
    position: absolute;
    background: #ffffff;
    width: 62px;
    height: 62px;
    transform: rotate(-45deg);
    border-top: 4px solid #1698d9;
    top: 10px;
    left: -31px; }
  .page-main__package:hover {
    border: 4px solid #2ea8e6;
    border-left: none; }
    .page-main__package:hover::after {
      border-bottom: 4px solid #2ea8e6;
      border-left: 4px solid #2ea8e6; }
    .page-main__package:hover::before {
      border-top: 4px solid #2ea8e6; }
    .page-main__package:hover .page-main__weight {
      background-color: #2ea8e6; }

.page-main__img-container {
  width: 252px;
  height: 270px;
  border-radius: 8px;
  overflow: hidden;
  position: absolute;
  left: -38.6px;
  bottom: 0.5px;
  z-index: 10; }

.page-main__img-container img {
  margin-left: -29px;
  margin-top: 2px; }

.page-main__weight {
  background-color: #1698d9;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  position: absolute;
  bottom: 12px;
  right: 12px;
  z-index: 20; }

.page-main__weight-count {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 42px;
  line-height: 42px;
  color: #ffffff; }

.page-main__weight-unit {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 21px;
  line-height: 22px;
  color: #ffffff; }

.page-main__promo-description {
  font-family: "TrebuchetMS", "Arial", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  margin: 15px 0 0 -44px;
  text-align: center; }

.page-main__buy-link {
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
  cursor: pointer; }
  .page-main__buy-link:hover {
    opacity: 0.7; }

.page-main__package--selected {
  border: 4px solid #d91667;
  border-left: none; }
  .page-main__package--selected::after {
    border-bottom: 4px solid #d91667;
    border-left: 4px solid #d91667; }
  .page-main__package--selected::before {
    border-top: 4px solid #d91667; }
  .page-main__package--selected .page-main__weight {
    background-color: #d91667; }
  .page-main__package--selected:hover {
    border: 4px solid #d91667;
    border-left: none; }
    .page-main__package--selected:hover::after {
      border-bottom: 4px solid #d91667;
      border-left: 4px solid #d91667; }
    .page-main__package--selected:hover::before {
      border-top: 4px solid #d91667; }
    .page-main__package--selected:hover .page-main__weight {
      background-color: #d91667; }

.page-main__package--select-hover {
  border: 4px solid #e62e7a;
  border-left: none; }
  .page-main__package--select-hover::after {
    border-bottom: 4px solid #e62e7a;
    border-left: 4px solid #e62e7a; }
  .page-main__package--select-hover::before {
    border-top: 4px solid #e62e7a; }
  .page-main__package--select-hover .page-main__weight {
    background-color: #e62e7a; }
  .page-main__package--select-hover:hover {
    border: 4px solid #e62e7a;
    border-left: none; }
    .page-main__package--select-hover:hover::after {
      border-bottom: 4px solid #e62e7a;
      border-left: 4px solid #e62e7a; }
    .page-main__package--select-hover:hover::before {
      border-top: 4px solid #e62e7a; }
    .page-main__package--select-hover:hover .page-main__weight {
      background-color: #e62e7a; }

.page-main__package--disabled {
  border: 4px solid #b3b3b3;
  border-left: none;
  cursor: default; }
  .page-main__package--disabled::after {
    border-bottom: 4px solid #b3b3b3;
    border-left: 4px solid #b3b3b3; }
  .page-main__package--disabled::before {
    border-top: 4px solid #b3b3b3; }
  .page-main__package--disabled .page-main__weight {
    background-color: #b3b3b3; }
  .page-main__package--disabled p,
  .page-main__package--disabled h3,
  .page-main__package--disabled h4,
  .page-main__package--disabled img {
    opacity: 0.5; }
  .page-main__package--disabled:hover {
    border: 4px solid #b3b3b3;
    border-left: none; }
    .page-main__package--disabled:hover::after {
      border-bottom: 4px solid #b3b3b3;
      border-left: 4px solid #b3b3b3; }
    .page-main__package--disabled:hover::before {
      border-top: 4px solid #b3b3b3; }
    .page-main__package--disabled:hover .page-main__weight {
      background-color: #b3b3b3; }

@media (max-width: 1023px) {
  .page-main__wrap {
    padding: 37px 40px 0; }
  .page-main__list {
    justify-content: center; }
  .page-main__item {
    margin-right: 40px; }
  .page-main__item:nth-child(2n) {
    margin-right: 0; } }

@media (max-width: 767px) {
  .page-main__wrap {
    padding: 30px 10px 0; }
  .page-main__list {
    flex-direction: column; }
  .page-main__item,
  .page-main__item:nth-child(2n),
  .page-main__item:last-child {
    width: 300px;
    margin: 0 auto 30px; } }
